	module.exports = {"pathContext":{"group":[{"node":{"fields":{"title":"Goose Dinner","date":"26 Dec 2003","slug":"/2003-12-26-goose-dinner/","tags":"Y2003"},"html":"<p>Chrismas isn't even over yet, but we are already getting ready for the next <a title=\"PapaScott : Christopher Ryan Hanson\" href=\"http://www.papascott.de/1999/12/27/\">big event</a>. As has been our tradition for the past 4 years, we will once again have <a title=\"PapaScott : Birthing Christopher\" href=\"http://www.papascott.de/1999/12/30/\">goose dinner</a> on December 26th. This year we have a goose breast, with red cabbage from the freezer, and potatoes for the non-Atkins eaters. And we have a story to tell to Christopher.</p>"},"next":{"fields":{"title":"Santa Has Landed","date":"24 Dec 2003","slug":"/2003-12-24-santa-has-landed/"}},"previous":{"fields":{"title":"Big Day","date":"27 Dec 2003","slug":"/2003-12-27-big-day/"}}},{"node":{"fields":{"title":"Santa Has Landed","date":"24 Dec 2003","slug":"/2003-12-24-santa-has-landed/","tags":"Y2003"},"html":"<p><img alt=\"crh_xmas04train.jpg\" src=\"http://www.papascott.de/wordpress/wp-content/uploads/2003/12/crh_xmas04train.jpg\" width=\"320\" height=\"240\" border=\"0\" /></p>\n<p>For those following Santa's progress, we can report that he arrived in Jesteburg at approximately 4 pm, leaving a Playmobil RC train in exchange for Christopher's entire pacifier collection. We hope that Santa will find good homes for the pacifiers.</p>"},"next":{"fields":{"title":"Espresso Love","date":"24 Dec 2003","slug":"/2003-12-24-espresso-love/"}},"previous":{"fields":{"title":"Goose Dinner","date":"26 Dec 2003","slug":"/2003-12-26-goose-dinner/"}}},{"node":{"fields":{"title":"Espresso Love","date":"24 Dec 2003","slug":"/2003-12-24-espresso-love/","tags":"Y2003"},"html":"<p>Doc Searls describes his <a title=\"The Doc Searls Weblog : Saturday, December 20, 2003\" href=\"http://doc.weblogs.com/2003/12/23#theBestChristmasGiftsAnyCoffeeFreakCanGetOrGive\">Starbucks Barista espresso machine</a>. It's his third machine, list price $399, 2 year guarantee, and \"if you use it heavily it should last at least that long\". We have a 10 year old Bosch Espresso Cup (mostly plastic, cost DM 99 at the time) and a &#8364;1.99 milk frother. And we buy pre-ground espresso from our discounter. What are we missing?</p>"},"next":{"fields":{"title":"Speech Therapy","date":"23 Dec 2003","slug":"/2003-12-23-speech-therapy/"}},"previous":{"fields":{"title":"Santa Has Landed","date":"24 Dec 2003","slug":"/2003-12-24-santa-has-landed/"}}}],"pathPrefix":"","first":false,"last":false,"index":564,"pageCount":1101}}

